// LAYOUT Atoms/colors/{solid,gradient} -> Storybook

.gradientStory {
  display: flex;
  margin-left: 1rem;
  background-color: #fff;

  svg {
    margin: 0.5rem;
    border-radius: 100%;
  }
}

.blockColor {
  margin: 0.5rem;
  font-size: 1.2rem;
  text-align: center;

  table {
    margin: 0;
    border-collapse: collapse;

    th,
    td {
      padding: 0.5em;
      border: 1px solid #dfe2e5;
      text-align-last: left;
    }
  }
}
